.button {
  background-color: rgb(33, 150, 243);
  color: rgb(255, 255, 255);
  border-radius: 100px;
  font-size: 15px;
  font-weight: 500;
  box-shadow: -2px 3px 3px rgba(0, 0, 0, 0.08);
  box-shadow: 2px 3px 3px rgba(0, 0, 0, 0.08);
}
.button__back {
  border-radius: 100px;
  margin: 50px auto 0px 32px;
  box-shadow: 1px 4px 6px rgba(0, 0, 0, 0.16);
  box-shadow: 0px 5px 3px rgba(0, 0, 0, 0.1);
  box-shadow: 2px 7px 4px rgba(0, 0, 0, 0.12);
}
.button__back:hover {
  transform: scale(1);
  animation: anim 1s linear both;
}
.button__back-link {
  display: flex;
  align-items: center;
  color: rgb(255, 255, 255);
  font-weight: 500;
  letter-spacing: 0.4px;
  line-height: 0.58;
  text-transform: uppercase;
  margin-right: 16px;
}
.button__back-icon {
  fill: rgb(255, 255, 255);
  display: block;
  width: 24px;
  height: 24px;
  margin: 8px 8px 8px 16px;
}
.button__decrement, .button__increment {
  height: 42px;
  width: 59px;
  border-radius: 4px;
}
.button__disabled {
  background-color: rgba(0, 0, 0, 0.08);
  color: rgba(0, 0, 0, 0.87);
  border-radius: 4px;
  font-size: 15px;
  font-weight: 500;
  box-shadow: -2px 3px 3px rgba(0, 0, 0, 0.08);
  box-shadow: 2px 3px 3px rgba(0, 0, 0, 0.08);
}
.button__clear {
  margin-top: 20px;
}
.button__login {
  margin-top: 16px;
  padding: 8px 0px;
}
@media (max-width: 678px) {
  .button__login {
    width: 90%;
  }
}
@media (min-width: 678px) {
  .button__login {
    padding: 8px 272px;
  }
}
.button__widget {
  margin-top: 40px;
  padding: 8px 22px;
  cursor: pointer;
}
.button__input-registration, .button__input-login {
  padding: 6px 16px;
  font-weight: 500;
  margin-top: 24px;
  line-height: 24px;
  text-transform: uppercase;
  letter-spacing: 0.4px;
}

@keyframes anim {
  100% {
    transform: scale(1.2);
  }
}
